import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import { Text, Localizer } from 'preact-i18n';

import mu from '../../lib/musher-util';
const MARGIN_TOP = mu.marginTop(true);
const MARGIN_BOTTOM = mu.marginBottom();
const SUB_MENU_MARGIN_TOP = mu.subMenuMarginTop();

import Error from '../../components/error';
import Message from '../../components/message';

@observer
class Profile extends Component {
	constructor(props) {
        super(props);
        this.state = {
            time: Date.now(),
			count: 10
        };
    }

	resetEmailMessage = () => {
		const { userStore } = this.props.stores;
		userStore.updateField('emailMessage', '');
		userStore.updateField('emailMessageIcon', '');
	}

	resetEmailError = () => {
		const { userStore } = this.props.stores;
		userStore.updateField('emailError', '');
		userStore.updateField('emailErrorIcon', '');
	}

	toggleSetting= async (field) => {
        const { userStore } = this.props.stores;
		const { user } = userStore;
		const { settings = {} } = user;
        await userStore.updateSetting({ [`settings.${field}`]: settings[field] === 1 ? 0 : 1 }, { [field]: settings[field] === 1 ? 0 : 1 });
    }

	subscriptionCreate = async () => {
		const { paypalStore, userStore } = this.props.stores;
		this.setState({ paypalLoading: true });
		await paypalStore.subscriptionCreate({});
		await userStore.getInfo();
		this.setState({ paypalLoading: false });
	}

	subscriptionCancel = async (e) => {
		const { id } = e.target.closest('button').dataset;
		const { paypalStore, userStore } = this.props.stores;
		await paypalStore.subscriptionCancel({ planId: id });
		await userStore.getInfo();
	}

	subscriptionDetail = async () => {
		const { paypalStore, userStore } = this.props.stores;
		const { user = {} } = userStore;
		const { subscription } = user;

		if (subscription.payPalSubscriptionId) {
			const response = await paypalStore.subscriptionDetail({ id: subscription.payPalSubscriptionId });
			userStore.setSubscriptionPaypal(response.data);
			await userStore.getInfo();
		}
	}

	toggleCancel = () => {
		const { showCancel } = this.state;
		this.setState({
			showCancel: !showCancel,
		});
	}
	toggleDetails = () => {
		const { showDetails } = this.state;
		if (!showDetails) {
			this.subscriptionDetail();
		}
		this.setState({
			showDetails: !showDetails,
		});
	}

	componentDidMount() {
		const { userStore } = this.props.stores;

		if (this.props.verifyEmailToken) {
			userStore.verifyEmail({ token: this.props.verifyEmailToken });
		}
	}

	render() {
		const { scrolledDown } = this.props;
		const { userStore, appState } = this.props.stores;
		const { darkmode } = appState;
		const { user, emailMessage, emailError, emailMessageIcon, emailErrorIcon, isAdmin } = userStore;
		const { teams, settings = {} } = user;
		const allTeams = userStore.findTeams(teams);
		const apiServer = util.getApiServer();
		const jwtToken = util.getJwtToken();
		return (
			<div class='container-fluid' style={`margin-bottom: ${MARGIN_BOTTOM}; margin-top: ${MARGIN_TOP};`}>
				<div class='row fixed-top' style={`margin-top: ${scrolledDown > 0 ? 0 : SUB_MENU_MARGIN_TOP}; background-color: ${darkmode ? '#191d21' : '#f8f9fa'}; transition: all 0.3s ease-in-out;`}>
					<div class='col text-center subtopmenu text-overflow'>
						<nobr><a href='/users' class='text-secondary stretched-link'><i class='fas fa-user' /> <Text id='users.title'>Din profil</Text></a></nobr>
					</div>
					<div class='col text-center subtopmenu text-overflow border-bottom border-primary'>
						<nobr><a href='/users/settings' class='text-primary stretched-link'><i class='fas fa-sliders-h' /> <Text id='users.settings'>Innstillinger</Text></a></nobr>
					</div>
					{isAdmin && <div class='col text-center subtopmenu text-overflow'>
						<nobr><a href='/users/subscription' class='text-secondary stretched-link'><i class='fas fa-signature' /> <Text id='users.subscriptions'>Abonnement</Text></a></nobr>
					</div>}
					<div class='col text-center subtopmenu text-overflow'>
						<nobr><a href='/users/integrations' class='text-secondary stretched-link'><i class='fas fa-plug' /> <Text id='users.integrations'>Integrasjoner</Text></a></nobr>
					</div>
				</div>

				<div class='row mt-5'>
					<div class='col-4 text-muted text-right'>
						<Text id='users.settings-hide-profile'>Skjul profilen</Text>
					</div>
					<div class='col-8'>
						<div class='custom-control custom-switch'>
							<input type='checkbox' class='custom-control-input' id='settingsHideProfile' onInput={() => this.toggleSetting('hideProfile')} checked={settings.hideProfile} />
							<label class='custom-control-label' for='settingsHideProfile' />
						</div>
						<small class='text-muted'><Text id='users.settings-hide-profile-help'>Dersom du ikke vil at andre skal kunne finne og be om å følge deg, så kan du skjule profilen din her.</Text></small>
					</div>
				</div>

				<div class='row mt-5'>
					<div class='col-4 text-muted text-right'>
						<Text id='users.settings-allow-racevet'>Tillat veterinær</Text>
					</div>
					<div class='col-8'>
						<div class='custom-control custom-switch'>
							<input type='checkbox' class='custom-control-input' id='settingsVeterinaryAllowed' onInput={() => this.toggleSetting('veterinaryAllowed')} checked={settings.veterinaryAllowed} />
							<label class='custom-control-label' for='settingsVeterinaryAllowed' />
						</div>
						<small class='text-muted'><Text id='users.settings-veterinary-help'>Dersom du ønsker at godkjente veterinærer skal kunne søke deg opp og legge inn vaksiner må du skru på her.</Text></small>
					</div>
				</div>

				<div class='row mt-4'>
					<div class='col-4 text-muted text-right'>
						<Text id='users.settings-hide-invite'>Skjul invitasjonsboks</Text>
					</div>
					<div class='col-8'>
						<div class='custom-control custom-switch'>
							<input type='checkbox' class='custom-control-input' id='settingsHideInvitation' onInput={() => this.toggleSetting('hideInvite')} checked={settings.hideInvite} />
							<label class='custom-control-label' for='settingsHideInvitation' />
						</div>
						<small class='text-muted'><Text id='users.settings-hide-invite-help'>Skjul invitasjonsboksen på forsiden.</Text></small>
					</div>
				</div>

				<div class='row mt-4'>
					<div class='col-4 text-muted text-right'>
						<Text id='users.settings-hide-settings'>Skjul innstillinger</Text>
					</div>
					<div class='col-8'>
						<div class='custom-control custom-switch'>
							<input type='checkbox' class='custom-control-input' id='settingsHideSettings' onInput={() => this.toggleSetting('hideSettings')} checked={settings.hideSettings} />
							<label class='custom-control-label' for='settingsHideSettings' />
						</div>
						<small class='text-muted'><Text id='users.settings-hide-settings-help'>Skjul innstillinger på forsiden.</Text></small>
					</div>
				</div>

				<div class='row mt-4'>
					<div class='col-4 text-muted text-right'>
						<Text id='users.settings-summary-email'>Oppsummeringer</Text>
					</div>
					<div class='col-8'>
						<div class='custom-control custom-switch'>
							<input type='checkbox' class='custom-control-input' id='settingsSummaryEmails' onInput={() => this.toggleSetting('summaryEmails')} checked={settings.summaryEmails} />
							<label class='custom-control-label' for='settingsSummaryEmails' />
						</div>
						<small class='text-muted'><Text id='users.settings-receive-summary-email'>Motta oppsummeringer via e-post</Text></small>
					</div>
				</div>

				<div class='row mt-4'>
					<div class='col-4 text-muted text-right'>
						<Text id='users.settings-notification-email'>Notifications</Text>
					</div>
					<div class='col-8'>
						<div class='custom-control custom-switch'>
							<input type='checkbox' class='custom-control-input' id='settingsNotificationEmails' onInput={() => this.toggleSetting('notificationEmails')} checked={settings.notificationEmails} />
							<label class='custom-control-label' for='settingsNotificationEmails' />
						</div>
						<small class='text-muted'><Text id='users.settings-receive-notification-email'>Receive notifications via email</Text></small>
					</div>
				</div>

				<div class='row mt-4'>
					<div class='col-4 text-muted text-right'>
						<Text id='users.settings-info-email'>Info</Text>
					</div>
					<div class='col-8'>
						<div class='custom-control custom-switch'>
							<input type='checkbox' class='custom-control-input' id='settingsInfoEmails' onInput={() => this.toggleSetting('infoEmails')} checked={settings.infoEmails} />
							<label class='custom-control-label' for='settingsInfoEmails' />
						</div>
						<small class='text-muted'><Text id='users.settings-receive-info-email'>Motta info via e-post</Text></small>
					</div>
				</div>

				<div class='row mt-4'>
					<div class='col-4 text-muted text-right'>
						<Text id='users.settings-partner-email'>Partners</Text>
					</div>
					<div class='col-8'>
						<div class='custom-control custom-switch'>
							<input type='checkbox' class='custom-control-input' id='settingsPartnerEmails' onInput={() => this.toggleSetting('partnerEmails')} checked={settings.partnerEmails} />
							<label class='custom-control-label' for='settingsPartnerEmails' />
						</div>
						<small class='text-muted'><Text id='users.settings-receive-partner-email'>Motta tilbud fra våre partnere via e-post</Text></small>
					</div>
				</div>

			</div>
		);
	}
}

export default Profile;
